body {
  background-color: #dccfbe;
  color: #3a2f2d;
}

.pep {
  background-color: #3a2f2d;
  color: #dccfbe;
  font-size: 5em;
  text-align: center;
  font-weight: bold;
  font-family: "Bookman Old Style", serif;
  width: 80vw;
  border: 4px solid #3d3729;
  border-radius: 10vw;
  padding: 5vw;
  margin: 0 auto;
  position: relative;
  cursor: pointer;
}

.pep:before {
  content: "";
  display: block;
  position: absolute;
  border-color: #3d3729 transparent;
  left: 60vw;
  top: -45px;
  width: 0;
  border-width: 0 25px 45px ;
  border-style: solid;
  transform: skew(55deg);
  background-color: transparent;
}

.logo {
  margin: 10vh auto 0;
  display: block;
  cursor: pointer;
}

/* make a mobile friendly version */
@media screen and (max-width: 600px) {
  .pep {
    font-size: 2em;
    width: 80vw;
    border-radius: 10vw;
    padding: 5vw;
  }
  .pep:before {
    left: 66vw;
    top: -25px;
    border-width: 0 15px 25px ;
  }
}